import Transform from '../../assets/lib/transform'
import AlloyTouch from '../../assets/lib/alloy_touch'
export default {
    props: {
        imgs: {
            type: Array,
            default: []
        },
        delay: {
            type: Number,
            default: 2000
        },
        autoPlay: {
            type: Boolean,
            dafault: true
        }
    },
    //props: ['imgs', 'delay', 'dotcolor'],
    mounted() {
        this.$nextTick(() => {
            const box = this.$refs.box
            const containerWidth = this.$refs.container.getBoundingClientRect().width
            let _this = this
            const options = {
                target: box,
                touch: box,
                vertical: false,
                property: 'translateX',
                min: -containerWidth * (this.imgs.length - 1),
                max: 0,
                step: containerWidth,
                spring: true,
                inertia: false,
                touchStart() {
                    if (_this.timer != null) {
                        clearInterval(_this.timer)
                        _this.timer = null
                    }
                },
                touchEnd(evt, v, index) {
                    var step_v = index * this.step * -1;
                    var dx = v - step_v;
                    if (v < this.min) {
                        _this.step = this.min
                    } else if (v > this.max) {
                        _this.step = this.max
                    } else if (Math.abs(dx) < 30) {
                        _this.step = step_v
                    } else if (dx > 0) {
                        _this.step = step_v + this.step
                    } else {
                        _this.step = step_v - this.step
                    }
                    this.to(_this.step);
                    if (this.autoPlay) {
                        _this.turnPlay(this.step)
                    }
                    return false
                },
                animationEnd(evt, v) {
                    _this.currentPage = this.currentPage + 1
                }
            }
            Transform(box)
            _this.touch = new AlloyTouch(options)
            if (this.autoPlay) {
                _this.turnPlay(this.step)
            }

        })
    },
    data() {
        return {
            //imgs: [],
            options: null,
            timer: null,
            touch: null,
            step: 0,
            currentPage: 1,
        }
    },
    beforeDestroy() {
        clearInterval(this.timer)
        this.timer = null
    },
    computed: {
        imgItemWidth() {
            return 100 / this.imgs.length + '%'
        },
        imgBoxWidth() {
            return 100 * this.imgs.length + '%'
        }
        // dotbackground () {
        //     return 
        // }
    },
    methods: {
        turnPlay(step) {
            this.timer = setInterval(() => {
                if (step <= this.touch.min) {
                    step = 0
                } else {
                    step -= this.touch.step
                }
                this.touch.to(step)
            }, this.delay)
        }
    }
}



