export default {
    name: 'z-header',
    props: {
        showBorder: {
            type: Boolean,
            default: true
        },
        title: {
            type: String,
            default: '主页'
        },
        showBack: {
            type: Boolean,
            default: false
        },
        showArrow: {
            type: Boolean,
            default: true
