import Search from 'vux/src/components/search'
export default {
    name: 'search',
    components: {
        Search
    },
    data() {
        return {
            results: [],
            value: 'test'
        }
    },
    methods: {
        resultClick(item) {
            window.alert('you click the result item: ' + JSON.stringify(item))
        },
        getResult(val) {
            this.results = val ? this.newResult(this.value) : []
        },
        newResult(val) {
            let rs = []
            for (let i = 0; i < 8; i++) {
                rs.push({
                    title: `${val} result: ${i + 1} `,
                    other: i
                })
            }
            return rs
        }
    }
}



