import Icon from '../common/Icon'
export default {
    name: 'zNumber',
    props: {
        item: {
            type: Object
        },
        currentNum: {
            type: Number,
            default: 0
        }
    },
    components: {
        Icon
    },
    data() {
        return {
            num: this.currentNum
        }
    },
    methods: {
        add() {
            this.num++
            //this.$store.dispatch('addToCart', this.item) //添加入购物车
            this.$emit('add', this.item)
        },
        minus() {
            if (this.num > 0) {
                this.num--
                //this.$store.dispatch('minusfromCart', this.item)
                this.$emit('minus', this.item)
            }

        },
        // addToCart (item) {
           
        // }
    }
}



